
.journal__main-content{
    display: flex;

    @media screen  and (max-width: 576px ){
        display: flex;
        flex-direction:column;
        width: auto;
    
    }
}

.journal__sidebar{
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 550px;
    @media screen  and (max-width: 576px ){
        height: 400px;
        padding: 0px;
       
     }

}
.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;

    h3{
        font-weight: lighter;

    }
}

.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;

    transition: color .3s ease;
    
    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries{
    margin-top: 15px;
    flex: 1 1 auto;
    //overflow-y: scroll;
    @media screen  and (max-width: 576px ){
        width: auto;
       
    }

}

.journal__entry {
    background-color: white;
    border-radius: 4px;
    color: $dark-grey;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow: hidden;
    @media screen  and (max-width: 576px ){
        width: auto;
       
    }

}

.journal__entry-pinture{
    height: 80px;
    width: 80px;

}
.journal__entry-body{
    padding: 10px;
    
}

.journal__entry-title{
    font-size: 14px;
    font-weight: bold;

}

.journal__entry-content{
    font-size: 10px;

}
.journal__entry-date-box{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5px;
    
}
.journal__entry-date-box span{
    font-size: 12px;
}